<template>
  <div
    class="modal fade"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="bsModal"
  >
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span v-if="isNew"></span>
            <span v-else></span>
          </h5>
          <button type="button" class="btn-close"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-3">
                <label for="image" class="form-label"></label>
                <input type="text" class="form-control" id="image" placeholder="" />
              </div>
              <div class="mb-3">
                <label for="customFile" class="form-label"
                  > 
                  <i class="fas fa-spinner fa-spin"></i>
                </label>
                <input
                  type="file"
                  id="customFile"
                  class="form-control"
                  ref="fileInput"
                  @change="uploadFile"
                />
              </div>
              <img class="img-fluid" alt="" :src="tempProduct.imageUrl" />
              <!--  -->
              <div class="mt-5">
                <div class="mb-3 input-group">
                  <input type="url" class="form-control form-control" placeholder="" />
                  <button type="button" class="btn btn-outline-danger"></button>
                </div>
                <button class="btn btn-outline-primary btn-sm d-block w-100"></button>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="mb-3">
                <label for="title" class="form-label"></label>
                <input
                  type="text"
                  class="form-control"
                  id="title"
                  v-model="tempProduct.title"
                  placeholder=""
                />
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="category" class="form-label"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="category"
                    v-model="tempProduct.category"
                    placeholder=""
                  />
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="unit"
                    v-model="tempProduct.unit"
                    placeholder=""
                  />
                </div>
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="origin_price" class="form-label"></label>
                  <input
                    type="number"
                    class="form-control"
                    id="origin_price"
                    v-model="tempProduct.origin_price"
                    placeholder=""
                  />
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label"></label>
                  <input
                    type="number"
                    class="form-control"
                    id="price"
                    v-model="tempProduct.price"
                    placeholder=""
                  />
                </div>
              </div>
              <hr />

              <div class="mb-3">
                <label for="description" class="form-label"></label>
                <textarea
                  type="text"
                  class="form-control"
                  id="description"
                  v-model="tempProduct.description"
                  placeholder=""
                ></textarea>
              </div>
              <div class="mb-3">
                <label for="content" class="form-label"></label>
                <textarea
                  type="text"
                  class="form-control"
                  id="content"
                  v-model="tempProduct.content"
                  placeholder=""
                ></textarea>
              </div>
              <div class="mb-3">
                <div class="form-check">
                  <input
                    class="form-check-input"
                    type="checkbox"
                    id="is_enabled"
                    v-model="tempProduct.is_enabled"
                  />
                  <label class="form-check-label" for="is_enabled">  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
            
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click.prevent="$emit('update-product', tempProduct)"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ModalMixin from '../mixins/ModalMixin';

export default {
  mixins: [ModalMixin],
  props: {
    product: {
      type: Object,
      // 
      default() {
        return {};
      },
    },
    isNew: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      modal: {},
      // modal
      tempProduct: {},
    };
  },
  watch: {
    product() {
      this.tempProduct = this.product;
    },
  },
  methods: {
    uploadFile() {
      // console.dir(updatedFile); .file[0]
      const updatedFile = this.$refs.fileInput.files[0];
      // form-data
      const formData = new FormData();
      // file-to-upload
      // APIinputname"file-to-upload"updatedFile
      formData.append('file-to-upload', updatedFile);
      const apiUrl = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`;
      // form
      this.$http.post(apiUrl, formData).then((res) => {
        this.$refs.fileInput.value = '';
        this.tempProduct.imageUrl = res.data.imageUrl;
      });
    },
  },
};
</script>
